import React from 'react'
import {Redirect, Route, Switch} from "react-router-dom";
import PreJunior from "./pages/PreJunior";
import {JUNIOR} from "./pages/JUNIOR";
import {JUNIOR_PLUS} from "./pages/JUNIOR+";
import Error404 from "./pages/Error404";

export const PATH = {
    PRE_JUNIOR: '/pre-junior',
    JUNIOR: '/junior',
    JUNIOR_PLUS: '/junior-plus'
}

function Routes() {
    return (
        <div>
            {/*Switch –≤—ã–±–∏—Ä–∞–µ—Ç –ø–µ—Ä–≤—ã–π –ø–æ–¥—Ö–æ–¥—è—â–∏–π —Ä–æ—É—Ç*/}
            <Switch>

                {/*–≤ –Ω–∞—á–∞–ª–µ –º—ã –ø–æ–ø–∞–¥–∞–µ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É '/' –∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º —Å—Ä–∞–∑—É –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É PRE_JUNIOR*/}
                {/*exact –Ω—É–∂–µ–Ω —á—Ç–æ–± —É–∫–∞–∑–∞—Ç—å –ø–æ–ª–Ω–æ–µ —Å–æ–≤–ø–æ–¥–µ–Ω–∏–µ (—á—Ç–æ –ø–æ—Å–ª–µ '/' –Ω–∏—á–µ–≥–æ –Ω–µ –±—É–¥–µ—Ç)*/}
                <Route path={'/'} exact render={() => <Redirect to={PATH.PRE_JUNIOR}/>}/>

                <Route path={PATH.PRE_JUNIOR} render={() => <PreJunior/>}/>
                <Route path={'/'} exact render={() => <Redirect to={PATH.JUNIOR}/>}/>

                <Route path={PATH.JUNIOR} render={() => <JUNIOR/>}/>
                <Route path={'/'} exact render={() => <Redirect to={PATH.JUNIOR_PLUS}/>}/>
                <Route path={PATH.JUNIOR_PLUS} render={() => <JUNIOR_PLUS/>}/>

                // add routes

                {/*—É —ç—Ç–æ–≥–æ —Ä–æ—É—Ç–∞ –Ω–µ—Ç –ø—É—Ç–∏, –æ–Ω –æ—Ç—Ä–∏—Å—É–µ—Ç—Å—è –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞—Ö–æ—á–µ—Ç –ø–æ–ø–∞—Å—Ç—å –Ω–∞ –Ω–µ—Å—É—â–µ—Å—Ç–≤—É—é—â—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É*/}
                <Route render={() => <Error404/>}/>

            </Switch>
        </div>
    )
}

export default Routes
