import React, {useState} from 'react'
import SuperRange from './common/c7-SuperRange/SuperRange'
import SuperDoubleRange from './common/c8-SuperDoubleRange/SuperDoubleRange'

function HW11() {
    const [value1, setValue1] = useState(0)
    const [value2, setValue2] = useState(100)

    const onChangeRange = (value: number | number[]) => {
        if (Array.isArray(value)) {
            setValue1(value[0])
            setValue2(value[1])
        }
    }

    return (
        <div>
            <hr/>
            homeworks 11

            {/*should work (–¥–æ–ª–∂–Ω–æ —Ä–∞–±–æ—Ç–∞—Ç—å)*/}
            <div>
                <span>{value1}</span>
                <SuperRange
                    onChangeRange={setValue1}
                    // —Å–¥–µ–ª–∞—Ç—å —Ç–∞–∫ —á—Ç–æ–± value1 –∏–∑–º–µ–Ω—è–ª–æ—Å—å
                />
            </div>

            <div>
                <span>{value1}</span>
                <SuperDoubleRange
                    // value={[value1, value2]}
                    min={value1}
                    max={value2}
                    value={[value1, value2]}
                    onChangeRange={onChangeRange}
                    // —Å–¥–µ–ª–∞—Ç—å —Ç–∞–∫ —á—Ç–æ–± value1 –∏ value2 –∏–∑–º–µ–Ω—è–ª–æ—Å—å
                />
                <span>{value2}</span>
            </div>

            <hr/>
            {/*–¥–ª—è –ª–∏—á–Ω–æ–≥–æ —Ç–≤–æ—Ä—á–µ—Å—Ç–≤–∞, –º–æ–≥—É –ø—Ä–æ–≤–µ—Ä–∏—Ç—å*/}
            {/*<AlternativeSuperRange/>*/}
            {/*<AlternativeSuperDoubleRange/>*/}
            <hr/>
        </div>
    )
}

export default HW11
