import React, {useState} from 'react'
import {homeWorkReducer} from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import s from './HW8.module.css'

export type UserType = {
    _id: number
    name: string
    age: number
}

const initialPeople = [
    {_id: 0, name: '–ö–æ—Ç', age: 3},
    {_id: 1, name: '–ê–ª–µ–∫—Å–∞–Ω–¥—Ä', age: 66},
    {_id: 2, name: '–ö–æ–ª—è', age: 16},
    {_id: 3, name: '–í–∏–∫—Ç–æ—Ä', age: 44},
    {_id: 4, name: '–î–º–∏—Ç—Ä–∏–π', age: 40},
    {_id: 5, name: '–ò—Ä–∏–Ω–∞', age: 55},
]

function HW8() {
    const [people, setPeople] = useState<Array<UserType>>(initialPeople) // need to fix any

    // need to fix any
    const finalPeople = people.map((p: UserType) => (
        <div key={p._id} className={s.item}>
            <p>{p.name}</p>
            <p>{p.age}</p>
        </div>
    ))

    const sortUp = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'up'}))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'down'}))
    const check18 = () => setPeople(homeWorkReducer(initialPeople, {type: 'check', payload: 18}))

    return (
        <div>
            <hr/>
            homeworks 8

            {/*should work (–¥–æ–ª–∂–Ω–æ —Ä–∞–±–æ—Ç–∞—Ç—å)*/}
            {finalPeople}

            <div className={s.wrapper}>
                <SuperButton onClick={sortUp} className={s.buttonWidth}>sort up</SuperButton>
                <SuperButton onClick={sortDown} className={s.buttonWidth}>sort reverse</SuperButton>
                <SuperButton onClick={check18} className={s.buttonWidth}>check18</SuperButton>
            </div>
            <div>sort down</div>

            <hr/>
            {/*–¥–ª—è –ª–∏—á–Ω–æ–≥–æ —Ç–≤–æ—Ä—á–µ—Å—Ç–≤–∞, –º–æ–≥—É –ø—Ä–æ–≤–µ—Ä–∏—Ç—å*/}
            {/*<AlternativePeople/>*/}
            <hr/>
        </div>
    )
}

export default HW8
